.radio-color {
    &::before {
        content: "";
        position: absolute;
        top: -6px;
        left: 0px;
    
        width: 18px;
        height: 18px;
    
        border: 1px solid transparent;
        border-radius: 50%;
      }        
}

.radio-color--red {
    &::before {
        background-color: #d74345;
    }
}

.radio-color--white {
    &::before {
        background-color: #ffffff;
        border: 1px solid #dddddd;
    }
}

.radio-color--black {
    &::before {
        background-color: #000000;
    }
}

.radio-color--gray {
    &::before {
        background-color: #cbcbcc;
    }
}

.radio-color--green {
    &::before {
        background-color: #88c504;
    }
}

