.main-select {
    position: relative;

    width: 160px;
    height: 40px;
}

.main-select__elements {
    margin: 0;
    padding: 0;

    list-style: none;

    background-color: #ffffff;

}

.main-select__list {
    margin: 0;
    padding: 0;

    box-sizing: border-box;
    width: 160px;

    position: absolute;

    list-style: none;

    cursor: pointer;

    display: none;
    
    border: 1px solid #dcdcdc;
    border-top: none;
    background-color: #ffffff;

    z-index: 1;
}

.main-select__arrow {
    content: "";
    position: absolute;

    width: 2px; 
    height: 0; 
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    
    border-top: 5px solid black;

    cursor: pointer;

    top: 18px;
    right: 17px;
}

.main-select__element {

    box-sizing: border-box;

    width: 160px;
    height: 40px;

    padding-left: 10px;
    padding-top: 6px;
    margin-right: 20px;

    width: 160px;
    height: 30px;

}

.main-select__element:hover {
    color: #000000;
}

.main-select__element--active {
    display: block;

    border: 1px solid #dcdcdc;

    border-bottom: 2px solid #000000; 
  
    height: 40px;
  padding-top: 10px;
}

@media (min-width: $middle) {
    .main-select {
        width: 220px;
    }

    .main-select__list {
        width: 220px;
    }

    .main-select__element {
        width: 220px;
    }
}

@media (min-width: $wide) {
    .main-select {
        width: 306px;
    }

    .main-select__list {
        width: 306px;
    }

    .main-select__element {
        width: 306px;
    }
}