.main-form__color-container {
    display: flex;

    width: 100%;

    margin-bottom: 22px;
}

.main-form_container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  margin-bottom: 5px;
}

.main-form__color {
    display: flex;
    flex-wrap: wrap;
}

.main-form__select {
    margin-right: 20px;
    margin-bottom: 54px;
}

.main-form {
    padding-left: 10px;
    padding-right: 10px;
}

.main-form__input {
    font: inherit;

    box-sizing: border-box;
    height: 40px;

    padding-left: 10px;
    padding-top: 15px;
    padding-bottom: 16px;

    border: 1px solid #dcdcdc;
    border-bottom-width: 2px; 
}

.main-form__option {
    font: inherit
}

.main-form__option:hover {
    color: #000000;

}

.main-form__select-no {
    display: none;
}

.main-form__color-discribe {
    margin: 0;
    margin-bottom: 14px;

    font-size: 12px;
}

.main-form__input:hover {
    cursor: pointer;
    border-bottom-color: #c4092f; 
}

.main-form__input:focus {
    color: #000000;
    border-bottom-color: #000000; 
}

.main-form__color-label {
    display: block;
    margin-right: 17px;
    width: 21px;
    height: 21px;
    position: relative;
}

.main-form__radio:checked + .main-form__color-label {
    &::before {
        top: -9px;
        left: -4px;
        border: 4px solid #c4092f;
      }        
}

.main-form__label {
    width: 100%;
    order: 1;
    margin-left: 9px;
    margin-bottom: 2px;
}

.main-form__input {
    margin-bottom: 9px;
    box-sizing: border-box;

    &--name {
        order: 1;
        width: 100%;
    }

    &--price {
        order: 3;
        width: 159px;
        margin-bottom: 31px;
    }

    &--year {
        order: 4;
        width: 161px;
    }

    &--discribe{
        order: 5;
        width: 100%;
    }
}

.main-form__container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    
}

.main-form__button {
    font: inherit;

    position: relative;

    box-sizing: border-box;
    width: 160px;
    height: 40px;
  
  padding-left: 24px;

    border-color: transparent;
    border-radius: 4px;
    background-color: #c4092f;

    color: #ffffff;
    font-size: 16px;
    font-weight: 700;
    line-height: 36px;
    text-transform: uppercase;
  text-align: left;

    cursor: pointer;
}

.main-form__button:before { 
    position: absolute; 
    content: ""; 
    right: 27px; 
    top: 34%; 

    width: 3px; 
    height: 7px;

    transform: rotate(-45deg); 
    background: #ffffff;
}
.main-form__button:after  { 
    position: absolute; 
    content: ""; 
    right: 27px; 
    top: 44%; 
    background: #ffffff; 
    width: 3px; 
    height: 7px;

    background: #ffffff; 

    transform: rotate(45deg); }

@media (min-width: $middle) {
    .main-form__input {
        margin-bottom: 29px;
    
        &--name {
            order: 2;
            width: 220px;
        }
    
        &--price {
            order: 4;
            width: 220px;
        }
    
        &--year {
            order: 3;
            width: 220px;
        }
    
        &--discribe{
          margin-bottom: 0px;
        }
    }

    .main-form__label {
        text-align: right;
        margin-right: 184px;
        margin-left: auto;
        order: 1;
    }
  
  .main-form__select {
    margin-bottom: 36px;
  }
  
  .main-form_container {
    margin-bottom: 14px;
  }
  
  .main-form__color-label {
    margin-right: 23px;
    &:last-child {
      margin-right: 0;
    }
  }

    .main-form__color-discribe {
        width: 100%;
      margin-bottom: 0;
    }

    .main-form__color-container {
        width: 34%;
        margin-top: 14px;
    }

    .main-form__color {
        justify-content: space-between;
        align-content: center;
    }

    .main-form__button {
        width: 220px;
        padding-left: 54px;
    }
  
  .main-form__button:after {
    right: 57px;
    top: 48%;
  }
  
  .main-form__button:before {
    right: 57px;
    top: 35%;
  }
}

@media (min-width: $wide) {
    .main-form__button {
        width: 306px;
      padding-left: 104px;
    }

    .main-form__color-container {
        width: 327px;
    }

    .main-form__input {
        width: 306px;
    }

    .main-form__input--discribe {
        width: 100%;
    }

    .main-form__select {
        margin-bottom: 158px;
    }
  
    .main-form__button:after {
    right: 91px;
  }
  
  .main-form__button:before {
    right: 91px;
  }
}