.main {
    width: 360px;
    margin: auto;
}

.main__title {
    color: #282d30;
    font-size: 42px;
    font-weight: 700;
    line-height: 42px;

    margin: 0;
    text-align: center;
    margin-bottom: 31px;
}

@media (min-width: $middle) {
    .main {
        width: 720px;
    }
  
  .main__title {
    margin-bottom: 39px;
  }
}

@media (min-width: $wide) {
    .main {
        width: $wide;
    }
}