.main-footer {
    width: 360px;
    margin: auto;
    min-height: 102px;
    background-color: #282d30;
  
    margin-top: auto;

    display: flex;

    &--copyright {
        width: 175px;

        color: #ffffff;
        font-family: Roboto;
        font-size: 11px;
        font-weight: 300; 

        margin: auto;
      margin-top: 37px;
        text-align: center;
    }
}

@media (min-width: $middle) {
    .main-footer {
        width: $middle;
    }
}

@media (min-width: $wide) {
    .main-footer {
        width: $wide;
    }
}