.page-header {
    min-height: 80px;
    width: 360px;
    
    margin: auto;
    margin-bottom: 31px;
  
    background-color: #282d30;
  }

.page-header__img {
  width: 297px;
  height: 63px;

  margin-left: 10px;
  margin-top: 14px;
}  

@media (min-width: $middle) {
  .page-header {
    height: 101px;
    width: 720px;

    display: flex;
    
    margin-bottom: 64px;
  }

  .page-header__img {
    width: 494px;
    height: 104px;
    margin-left: 87px;
    margin-top: 0;
    transform: translateY(4px)
  }
} 

@media (min-width: $wide) {
  .page-header {
    width: 100%;
  }
  
   .page-header__img {
    width: 512px;
    margin-left: -21px;
    transform: translateY(4px)
  }
  
   .page-header__logo {
    margin-left: auto;
     margin-right: auto;
  }
} 