.main-table__value {
    display: none;
}

.main-table__table {
  border-collapse: collapse;

  font-size: 14px;
  font-weight: 300;
  color: #282d30;
}

.main-table__section {
  width: 360px;
  box-sizing: border-box;

  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  align-items: baseline;
  

  background-color: #f5f6f6;

  border-top: 1px solid #e2e3e3;
  
  padding: 19px 26px 16px 18px;
}

.main-table__section--discribe {

    padding: 21px 26px 16px 18px;
}

.main-table__element--button {
  font: inherit;

  width: 73px;
  height: 29px;
  background-color: #282d30;

  box-sizing: border-box;

  text-align: center;
  color: #ffffff;

  border-color: transparent;
  border-radius: 20px;

  cursor: pointer;
  
  transform: translate(6px,-2px);
}

.main-table__element--color {
  width: 10px;
}

.main-table__radio {
  &::before {
    top: -17px;
    left: -5px;
  }
}

.main-table__element-container {
  padding: 0;
}

.main-table__section--discribe {
  padding-top: 10px;
}

.main-table__title {
  display: none;
}

.main-table__element {
  padding: 0;

  &--name {
    width: 130px;
    margin-right: 30px;
    margin-bottom: 10px;
  };  

  &--discribe {
   display: none;
    
    width: 100%;

    font-size: 13px;
    color: #8b9497;
    margin-bottom: 5px;
  };  

  &--price {
    width: 30%;
    text-align: right;
  };  

  &--button {
    margin-left: auto;
  }

  &--status {
    margin-right: auto;
  }

  &--year {
    margin-right: 27px;
  }

  &--color {
    position: relative;
    width: 10px;
  }
}


.main-table__element--visual {
      display: block;
}

@media (min-width: $middle) {
  .main-table__value {
    display: flex;
    justify-content: space-between;

    padding: 14px 175px 14px 15px;

    background-color: transparent;
    border-radius: 5px 5px 0 0; 

    font: inherit;
    color: #ffffff;
    font-size: 15px;
    font-weight: 500;
    background-color: #c4092f;

}

  
  .main-table__table {
    font-size: 15px;
    box-sizing: border-box;
    width: 700px;
    margin-left: 10px;
    margin-right: 10px;
  }

 .main-table__value-element {
   padding: 0;
   &--name {
     margin-right: 197px;
   }
   &--year {
    margin-right: 20px;
  }
  &--color {
    margin-right: 16px;
  }
  &--status {
    margin-right: 40px;
  }
  &--price {
    margin-left: auto;
  }

 }

.main-table__title {
  display: block;
  position: relative;
  margin: 0;

  margin-left: 23px;

  height: 16px;
  color: #282d30;
  font-size: 20px;
  font-weight: 700;
  text-transform: uppercase;

  margin-bottom: 30px;
}

.main-table__title::before {
  content: "";
  position: absolute;

  left: -14px;

  width: 3px;
  height: 18px;
  background-color: #8b9497;
}

.main-table {
  margin-bottom: 427px;
}
  
  .main-table__section {
    justify-content: flex-start;
    width: 100%;
    padding-right: 11px;
    padding-top: 6px;
    padding-bottom: 8px;
  }
  
    .main-table__section--discribe {
  padding-top: 9px;
    padding-bottom: 10px;
    padding-right: 11px;
    padding-bottom: 0;
}


.main-table__element {

  &--name {
    order: 0;
    width: 260px;
    margin-right: 0;
    margin-bottom: 0;
    white-space: nowrap; 
    overflow: hidden;
    text-overflow: ellipsis;
  }  

  &--discribe {
    order: 7;
    width: 100%;

    font-size: 13px;
    color: #8b9497;
    
    margin-bottom: 0;
    transform: translateY(-4px)
  }  

  &--price {
    order: 4;
    width: 100px;
    margin-right: 10px;
    text-align: left;
  }  

     &--button {
        height: 29px;
       width: 83px;
      color: #8b9497;
      background-color: transparent;
   } 

  &--status {
    order: 3;
   width: 120px;
    margin-right: 0;
  }

  &--year {
    order: 1;
    margin-right: 26px;
  }

  &--color {
    margin-right: 25px;
    order: 2;
  }
}
    
  .main-table__element--button:hover {

  color: #ffffff;
  background-color: #282d30;
}

  .main-table__element-span {
    display: block;
    width: 225px;
  }

  .main-table__element-container {
    order: 6;
  }
}

@media (min-width: $wide) {

  .main-table__title {
    margin-bottom: 27px;
  }

.main-table {
    margin-bottom: 320px;
}
  
  .main-table__table {
    width: 959px;
  }
  
  .main-table__element {

  &--name {
    width: 290px;
    margin-right: 169px;

  }  

  &--discribe {

    width: 100%;

  }  

  &--price {
    width: 100px;
    margin-right: 25px;
  }  
    
  &--status {
    margin-right: 6px;
  }

  &--year {
    order: 1;
    margin-right: 42px;
  }

  &--color {
    margin-right: 40px;
  }
}
  
   .main-table__value-element {
   &--name {
     margin-right: 393px;
   }
   &--year {
    margin-right: 38px;
  }
  &--color {
    margin-right: 20px;
  }
  &--status {
    margin-right: 40px;
  }
  &--price {
    margin-right: 23px;
  }
}
     
  .main-table__element-span {
    width: 100%
  }
     
  .main-table__section--discribe {

    padding-bottom: 10px;
  }
}